import React, { Component } from 'react';
import { Table, Input, DatePicker, Button, message } from 'antd';
import { postAction } from '@/axios';
import moment from 'moment';

export default class SummaryModalStep2 extends Component {
    state = {
        columns:[
            {
                title:"序号",
                dataIndex:"sortIndex",
                width:"80px"
            },{
                title:"填报计划名称",
                dataIndex:"planTaskName",
                render:(text)=><span title={text}>{text}</span>
            },{
                title:"填报时间",
                width:"260px",
                render:(text,record)=>{
                    let taskStartTime =record.taskStartTime?moment.unix(record.taskStartTime/1000).format("YYYY-MM-DD"):"无";
                    let taskEndTime =record.taskEndTime?moment.unix(record.taskEndTime/1000).format("YYYY-MM-DD"):"无";
                    // return 
                    let title = `${taskStartTime} - ${taskEndTime}`
                    return <span title={title}>{title}</span>
                }
            },{
                title:"填报人数",
                dataIndex:"userNum",
                width:"100px"
            },{
                title:"发起人",
                dataIndex:"creatorName"
            },{
                title:"发起部门",
                dataIndex:"creatorDepartName"
            }
        ],
        dataSource:[
            // {sortIndex:"1",planTaskName:"mingc",taskStartTime:12345,taskEndTime:78654,userNum:"3",creatorName:"creator",creatorDepartName:"depart",planTaskId:"001"},
        ],
        pagination:{
            current:1,
            pageSize:10,
            toatl:1,
            showTotal: (total) => (`共${total}条`),
            showQuickJumper:true,
        },
        filterValue:{
            planTaskName:"",
            time:[],
        }
    }
    componentDidMount(){
        this._getDataRequest();
    }
    //获取列表请求
    _getDataRequest = (byPage) => {
        let pageNum = 1;
        let pageSize = this.state.pagination.pageSize;
        if(byPage==="bypage"){
            pageNum = this.state.pagination.current
        }
        let params = {
            planTaskName:this.state.filterValue.planTaskName||"",
            creatorName:"",
            createTimeStart:this.state.filterValue.time.length>0?moment(this.state.filterValue.time[0]).valueOf():"",
            createTimeEnd:this.state.filterValue.time.length>0?moment(this.state.filterValue.time[1]).valueOf():""
        }
        // console.log(params);
        postAction(`/ybt/summary/data/plan/list?templetId=${this.props.templetId[0]}&pageNum=${pageNum}&pageSize=${pageSize}`,params).then(res=>{
            if(res.success){
                this.setState((state)=>{
                    return {
                        dataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item})),
                        pagination:{...state.pagination,current:res.obj.page}
                    }
                })
            }else{
                message.error(res.obj)
            }
        })
        
        //发送请求
        // console.log(pageNum,pageSize,params)
    }
    //翻页查询
    _changePagination = (pagination) => {
        this.setState((state)=>{
            return {...state.pagination,current:pagination.current}
        },()=>{this._getDataRequest("bypage")})
    }
    //修改计划名称
    _changeName = (e) => {
        e.persist();//取不到e.target需要执行该操作
        this.setState((state)=>{
            return {
                filterValue:{...state.filterValue,planTaskName:e.target.value}
            }
        })
    }
    //修改填报时间
    _changeTime = (date,dataString) => {
        this.setState((state)=>{
            return {
                filterValue:{...state.filterValue,time:dataString}
            }
        })
    }
    //搜索
    _search = () => {
        this._getDataRequest();
    }
    render() {
        return (
            <React.Fragment>
                <div style={{marginBottom:"10px"}}>
                    <span>填报计划：</span><Input placeholder="请输入计划名称" onChange={this._changeName} style={{width:"240px"}}/>
                    <span style={{marginLeft:"20px"}}>填报完成时间：</span><DatePicker.RangePicker format="YYYY-MM-DD HH:mm:ss" onChange={this._changeTime} style={{width:"240px"}}/>
                    <Button style={{marginLeft:"20px"}} icon="search" onClick={this._search}>搜 索</Button>
                </div>
                <Table
                    rowKey="planTaskId"
                    columns={this.state.columns}
                    dataSource={this.state.dataSource}
                    pagination={this.state.pagination}
                    onChange={this._changePagination}
                    rowSelection={{
                        type:"checkbox",
                        onChange:(selectedRowKeys,selectedRows)=>{
                            this.props.onChange(selectedRowKeys);
                        },
                        selectedRowKeys:this.props.selectedRowKeys
                    }}
                />
            </React.Fragment>
            
        )
    }
}