import React, { Component } from 'react';
import { Input, DatePicker, Select, Button, Form } from 'antd';
// import moment from 'moment';
import styles from './index.module.less';
import { postAction }  from '@/axios';

const { RangePicker } = DatePicker;

class SearchBox extends Component {
    constructor(props){
        super(props);
        this.state = {
            name:"",//汇总人
            // rangeTime:["2019-08-21", "2019-09-17"],
            rangeTime:[],//汇总时间
            templetId:"",//汇总表
            summaryFormList:[
                // {label:"全部",value:""},
            ]
        }
    }
    componentDidMount(){
        // this.search();
        this._getSummaryFormListRequest();
    }
    //获取汇总表列表
    _getSummaryFormListRequest = () => {
        postAction(`/ybt/summary/list?pageNum=1&pageSize=0`).then(res=>{
            if(res.success){
                this.setState((state)=>{
                    return {
                        summaryFormList:[
                            {label:"全部",value:""},
                            ...res.obj.rows.map(item=>({label:item.templetName,value:item.templetId}))
                        ]
                    }
                })
            }
        })
    }
    search = (e) => {
        e && e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            let params = {
                name:values.name || "",
                createTimeStart:values.time?values.time[0].format("YYYY-MM-DD HH:mm:ss") :"",
                createTimeEnd:values.time?values.time[1].format("YYYY-MM-DD HH:mm:ss") : "",
                templetId:values.templetId || "",
            }
            // console.log(params)
            this.props.search(params);
          }
        });
    }
    _reset = () => {
        this.props.form.resetFields();
        setTimeout(()=>{
            this.props.search();
        },0)
    }
    
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <div className={styles.filterBox}>
                <Form onSubmit={this.search} layout={"inline"}>
                    <Form.Item  label="汇总时间：">
                        {getFieldDecorator('time')(
                            <RangePicker style={{width:330}} format="YYYY-MM-DD HH:mm:ss" showTime={{ format: 'HH:mm:ss' }}/>
                        )}
                    </Form.Item>
                    <Form.Item  label="汇总人：">
                        {getFieldDecorator('name')(
                            <Input style={{width:220}} placeholder="请输入汇总人姓名" />
                        )}
                    </Form.Item>
                    <Form.Item  label="汇总表：">
                        {getFieldDecorator('templetId',{initialValue:""})(
                            <Select style={{width:180}}>
                                {this.state.summaryFormList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item>
                        <Button ghost icon="search" type="primary" htmlType="submit">搜索</Button>
                    </Form.Item>
                    <Form.Item>
                        <Button icon="sync" onClick={this._reset}>重置</Button>
                    </Form.Item> 
                </Form>
            </div>
        )
    }
}

export default Form.create()(SearchBox)