import React from 'react';
import PropTypes from 'prop-types';
import {getAction} from '@/axios';
import { message } from 'antd';

let hasDocsAPI = false;
class PreviewDoc extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            config:{
                documentServerApiUrl:'',documentType:'',fileType:'',key:'',title:'',url:''
            }
        }
    }
    componentDidMount(){
        this._getFormTaskInfo();
    }
    _getFormTaskInfo = () => {
        // let orginUrl = '10.175.205.18:8183';//测试时localhost得改成代理地址
        let orginUrl = window.location.host;
        if(this.props.type==='formTaskId'){//表单任务预览
            getAction(`/ybt/form/preview/formTask/info?formTaskId=${this.props.id}`).then(res=>{
                if(res.success){
                    let info = {...res.obj,url:`http://${orginUrl}/ybt/form/preview/formTask;JSESSIONID=${res.obj.sessionId}?formTaskId=${this.props.id}`};
                    this._setConfig(info)
                }else{
                    message.error(res.obj)
                }
            })
        }else if(this.props.type==='formId'){//表单预览
            getAction(`/ybt/form/preview/form/info?formId=${this.props.id}`).then(res=>{
                if(res.success){
                    let info = {...res.obj,url:`http://${orginUrl}/ybt/form/preview/form;JSESSIONID=${res.obj.sessionId}?formId=${this.props.id}`};
                    this._setConfig(info)
                }else{
                    message.error(res.obj)
                }
            })
        }else if(this.props.type==='summary'){
            getAction(`/ybt/summary/preview/info?templetId=${this.props.id}`).then(res=>{
                if(res.success){
                    let info = {...res.obj,url:`http://${orginUrl}/ybt/summary/data/preview;JSESSIONID=${res.obj.sessionId}?templetId=${this.props.id}&historyId=${this.props.historyId}`};
                    this._setConfig(info)
                }else{
                    message.error(res.obj)
                }
            })
        }else if(this.props.type==='summaryPreview'){
            getAction(`/ybt/summary/preview/info?templetId=${this.props.id}`).then(res=>{
                if(res.success){
                    let info = {...res.obj,url:`http://${orginUrl}/ybt/summary/data/pre/preview/ids;JSESSIONID=${res.obj.sessionId}?templetId=${this.props.id}&planTaskIds=${this.props.planTaskIds.join(',')}`};
                    this._setConfig(info)
                }else{
                    message.error(res.obj)
                }
            })
        }
    }
    _setConfig = (info) => {
        let documentScript = document.getElementById('documentScript');
        if(documentScript){
            let script = document.getElementById('documentScript');
            script.src = info.documentServerApiUrl;
        }else{
            let script = document.createElement('script');
            script.id = 'documentScript'
            script.type = 'text/javascript';
            script.async = false;
            script.src = info.documentServerApiUrl;
            document.head.appendChild(script);
        }
        this.setState({config:info},this._openEditor)
    }
    _openEditor = () => {
        let info = this.state.config;
        let DocsAPI = window.DocsAPI;
        if(DocsAPI){
            hasDocsAPI = true;
        }
        if(hasDocsAPI){
            let canDownload =  getAction('/ybt/form/task/getFormTaskStatus?formTaskId='+this.props.id).then(res=>{
                if(res.success){
                     if(res.obj===1){
                        return true
                     }else{
                         return false;
                     }
                }else{
                    return Promise.reject(res.obj)
                }
            })
            canDownload.then(canDownload=>{
                hasDocsAPI = true;
                let ticket = localStorage.getItem("ticket");
                if (ticket) {
                    info.url = info.url + "&ticket=" + ticket
                }
                window.docEditor = new DocsAPI.DocEditor("PreviewDoc",
                {
                    documentType: info.documentType,
                    document: {
                        fileType: info.fileType,
                        key: info.key,
                        title: info.title,
                        url: info.url,
                        permissions:{
                            download:this.props.type==="formId"?true:canDownload,//下载权限
                            print:this.props.type==="formId"?true:canDownload,//打印
                            edit: false,
                            fillForms: false,
                            comment:false,
                        },
                    },
                    editorConfig: {
                        mode: "view",
                        lang: "zh",
                        customization:{
                            chat:false,//聊天
                            comments:false,//评论
                            help:false,//帮助菜单
                            "autosave": true,
                            "commentAuthorOnly": false,
                            "compactToolbar": false,
                            logo:{
                                image:null,
                                url:null,
                                imageEmbedded:null
                            },
                            about:false,
                        },
                        plugins:{
                            autostart:[],
                            pluginsData:[]
                        }
                    },
                    height: "100%",
                    width: "100%",
                });
            }).catch(res=>{
                message.warn(res.obj);
            })
        }else{
            hasDocsAPI = false;
            setTimeout(()=>{
                this._openEditor()
            },0)
        }  
    }
    render(){
        return (
            <div id='PreviewDoc'>
                加载中。。。。。。
            </div>
        )
    }
}

PreviewDoc.defaultProps = {
    type:'formTaskId'
}

PreviewDoc.PropType = {
    id:PropTypes.string.isRequired,//表单任务id
    type:PropTypes.string.isRequired,//预览类型，'formTaskId'：表单任务，'formId'：表单,'summary':汇总
    historyId:PropTypes.string,//汇总需要用
}

export default PreviewDoc;