import React, { Component } from 'react';
import { Table, message } from 'antd';
import { postAction } from '@/axios';

export default class SummaryModalStep1 extends Component {
    state = {
        columns:[
            {
                title:"序号",
                dataIndex:"sortIndex"
            },{
                title:"汇总表名称",
                dataIndex:"templetName",
                render:(text)=><span title={text}>{text}</span>
            },{
                title:"最后修改时间",
                dataIndex:"updateTime"
            },{
                title:"创建人",
                dataIndex:"createBy"
            },{
                title:"创建部门",
                dataIndex:"createDeptName"
            }
        ],
        dataSource:[
            // {sortIndex:"1",templetName:"mingc",updateTime:"time",createBy:"creator",createDeptName:"depart",templetId:"001"},
        ],
        pagination:{
            current:1,
            pageSize:10,
            toatl:1,
            showTotal: (total) => (`共${total}条`),
            showQuickJumper:true,
        }
    }
    componentDidMount(){
        this._getDataRequest();
    }
    //获取列表请求
    _getDataRequest = (byPage) => {
        let pageNum = 1;
        let pageSize = this.state.pagination.pageSize;
        if(byPage==="bypage"){
            pageNum = this.state.pagination.current
        }
        postAction(`/ybt/summary/data/templet/list?pageNum=${pageNum}&pageSize=${pageSize}`).then(res=>{
            if(res.success){
                this.setState((state)=>{
                    return {
                        dataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item})),
                        pagination:{...state.pagination,current:res.obj.page}
                    }
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //翻页查询
    _changePagination = (pagination) => {
        this.setState((state)=>{
            return {...state.pagination,current:pagination.current}
        },()=>{this._getDataRequest("bypage")})
    }
    render() {
        return (
            <Table
                rowKey="templetId"
                columns={this.state.columns}
                dataSource={this.state.dataSource}
                pagination={this.state.pagination}
                onChange={this._changePagination}
                rowSelection={{
                    type:"radio",
                    onChange:(selectedRowKeys)=>{
                        this.props.onChange(selectedRowKeys);
                    },
                    selectedRowKeys:this.props.selectedRowKeys
                }}
            />
        )
    }
}
