/**
 * @description: 汇总记录页列表组件
 */
import React, { Component } from 'react';
import { Table, Divider, Modal, message } from 'antd';
import PlanListModal from './planListModal';
import {hasButtonPermission} from '@/util';
import { postAction } from '@/axios';
import PreviewDoc from '@/pages/common/PreviewDoc';
// import moment from 'moment'

export default class IndexTable extends Component {
    constructor(props){
        super(props);
        this.state = {
            columns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:"80px"
                },{
                    title:"汇总表名称",
                    dataIndex:"templetTable",
                    render:(text)=><span title={text}>{text}</span>
                },{
                    title:"包含填报计划",
                    dataIndex:"planTaskNumber",
                    width:"120px",
                    render:(text,record)=><span className="operation-link" onClick={()=>{this._showPlanList(record)}}>{text||0}</span>
                },{
                    title:"汇总时间",
                    dataIndex:"createTime",
                    width:"160px",
                    // render:(text)=> moment.unix(parseInt(text/1000)).format("YYYY-MM-DD")
                },{
                    title:"汇总人",
                    dataIndex:"name",
                    width:"160px",
                },{
                    title:"汇总结果",
                    width:"160px",
                    render:(text,record)=>{
                        return <React.Fragment>
                            {
                                hasButtonPermission("summaryRecord002")?<span className="operation-link" onClick={()=>{this._preview(record)}}>预览打印</span>:null
                            }
                            <Divider type="vertical"/>
                            {
                                hasButtonPermission("summaryRecord003")?<span className="operation-link" onClick={()=>{this._export(record)}}>导出</span>:null
                            }
                        </React.Fragment>
                    }
                }
            ],
            pagination:{
                current:1,
                pageSize:10,
                showTotal: (total) => (`共${total}条数据`),
                showQuickJumper:true,
            },
            dataSource:[
                // {sortIndex:"1",templetTable:"name",planTaskNumber:"2",createTime:"2019-11-12 12：20：20",createBy:"name",historyId:"1"}
            ],
            showPlanListModal:false,//显示包含填报计划弹窗
            historyId:"",//当前选择的汇总记录id
            pdfRecord:{},//当前预览的记录信息
            showPDF:false,
        }
    }
    componentDidMount(){
        // console.log(this.props.filterValue)
        this._getDataRequest();
    }
    componentDidUpdate(preProps){
        if(JSON.stringify(preProps.filterValue)!==JSON.stringify(this.props.filterValue)){
            // console.log("更新了搜索条件",this.props.filterValue);
            this._getDataRequest()
        }
    }
    //加载列表数据
    _getDataRequest = (bypage) => {
        let pageNum = 1;
        let pageSize = this.state.pagination.pageSize;
        if(bypage==="bypage"){
            pageNum = this.state.pagination.current
        }
        let params = {
            ...this.props.filterValue
        }
        // console.log(params)
        postAction(`/ybt/summary/history/list?pageNum=${pageNum}&pageSize=${pageSize}`,params).then(res=>{
            // console.log(res);
            if(res.success){
                this.setState((state)=>{
                    return {
                        dataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item})),
                        pagination:{...state.pagination,current:res.obj.page}
                    }
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //打开包含填报计划弹窗
    _showPlanList = (record) => {
        this.setState({
            showPlanListModal:true,
            historyId:record.historyId
        })
    }
    //预览
    _preview = (record) => {
        this.setState({
            showPDF:true,
            pdfRecord:record
        });
    }
    //导出
    _export = (record) => {
        // console.log("导出"+record);
        window.open(`http://${window.location.host}/ybt/summary/data/export?templetId=${record.templetId}&historyId=${record.historyId}`)
    }
    //翻页查询
    _changePagination = (pagination) => {
        this.setState((state)=>{
            return {...state.pagination,current:pagination.current}
        },()=>{this._getDataRequest("bypage")})
    }
    //关闭包含填报计划弹窗
    _closeModal = () => {
        this.setState({showPlanListModal:false,historyId:""})
    }
    render() {
        return (
            <React.Fragment>
                <Table
                    rowKey="historyId"
                    columns={this.state.columns}
                    dataSource={this.state.dataSource}
                    pagination={this.state.pagination}
                    onChange={this._changePagination}
                />
                <Modal
                   visible={this.state.showPlanListModal}
                   title="包含填报计划" 
                   footer={false}
                   onCancel={this._closeModal}
                   destroyOnClose={true}
                   width={1000}
                >
                    <PlanListModal historyId={this.state.historyId}/>
                </Modal>
                <Modal
                    visible={this.state.showPDF}
                    title={<span style={{fontWeight:"bold"}}>预览打印</span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <PreviewDoc id={this.state.pdfRecord.templetId} historyId={this.state.pdfRecord.historyId} type='summary'/>
                </Modal>
            </React.Fragment>
            
        )
    }
}
