/**
 * @description: 记录包含计划弹窗页
 */
import React, { Component } from 'react';
import { Table, message } from 'antd';
import { postAction } from '@/axios';

export default class PlanListModal extends Component {
    state={
        pagination:{
            current:1,
            pageSize:10,
            total:0,
            showTotal:(total)=>`共${total}条`,
            showQuickJumper:true,
        },
        columns:[
            {
                title:"序号",
                dataIndex:"sortIndex"
            },{
                title:"计划名称",
                dataIndex:"planTaskName"
            },{
                title:"实际发起时间",
                dataIndex:"taskStartTime"
            },{
                title:"实际结束时间",
                dataIndex:"taskEndTime"
            },{
                title:"发起人",
                dataIndex:"creatorName"
            },{
                title:"发起部门",
                dataIndex:"departName"
            },
        ],
        dataSource:[
            // {sortIndex:"1",planTaskName:"计划名称",taskStartTime:"发起时间",taskEndTime:"结束时间",creatorName:"发起人",departName:"发起部门"},
        ]
    }
    componentDidMount(){
        this._getData();
    }
    //翻页查询
    _changePage = (pagination) => {
        this.setState((state)=>{
            return {
                pagination:{...state.pagination,current:pagination.current}
            }
        },()=>{this._getData("page")})
    }
    //获取数据
    _getData = (byPage) => {
        let pageNum = 1;
        let pageSize = this.state.pagination.pageSize;
        if(byPage==="page"){
            pageNum = this.state.pagination.current
        }
        let historyId = this.props.historyId;
        postAction(`/ybt/summary/planRange/list?historyId=${historyId}&pageNum=${pageNum}&pageSize=${pageSize}`).then(res=> {
            console.log(res);
            if(res.success){
                this.setState((state)=>{
                    return {
                        dataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item})),
                        pagination:{...state.pagination,current:res.obj.page}
                    }
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    render() {
        return (
            <Table
                rowKey="sortIndex"
                columns={this.state.columns}
                dataSource={this.state.dataSource}
                pagination={this.state.pagination}
                onChange={this._changePage}
            />
        )
    }
}
