import React, { Component } from 'react';
import { Button, message,Modal } from 'antd';
import { postAction } from "@/axios";
import Step1 from './summaryModalStep1';
import Step2 from './summaryModalStep2';
import PreviewDoc from '@/pages/common/PreviewDoc';

export default class SummaryModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            showPDF:false,
            templetId:[],
            fieldList:[],
            step:1,
        }
    }
    //取消按钮--不用重置列表
    _cancle = () => {
        this.props.onCancel();
    }
    //下一步
    _nextStep = () => {
        if(this.state.templetId.length===0){
            message.warning("请选择一个汇总数据表");
            return false;
        }
        this.setState({step:2});
        this.props.changeStep(2);
    }
    //上一步
    _preStep = () => {
        this.setState({step:1});
        this.props.changeStep(1);
    }
    //第一步修改选择的汇总数据
    _changeTempletId = (templetId) => {
        this.setState({templetId})
    }
    //第二部修改数据范围
    _changePlanList = (fieldList) => {
        this.setState({fieldList})
    }
    //预览
    _preview = () => {
        if(this.state.fieldList.length===0){
            message.warning("请至少选择一个汇总数据");
            return false;
        }
        this.setState({showPDF:true})
        // let params = this.state.fieldList;
        // postAction(`/ybt/summary/data/pre/preview?templetId=${this.state.templetId[0]}`,params).then(res=>{
        //     if(res.success){
        //         let id=window.open("","_blank");
        //         id.document.body.innerHTML=res.obj
        //     }else{
        //         message.error(res.obj)
        //     }
        // })
    }
    //导出并完成
    _export = () => {
        if(this.state.fieldList.length===0){
            message.warning("请至少选择一个汇总数据");
            return false;
        }
        let param = this.state.fieldList;
        postAction(`/ybt/summary/data/save?templetId=${this.state.templetId[0]}`,param).then(res => {
            console.log(res);
            if(res.success){
                // this._cancle();
                window.location.reload();
                window.open(`http://${window.location.host}/ybt/summary/data/export?templetId=${this.state.templetId[0]}&historyId=${res.obj}`)
            }else{
                message.error(res.obj)
            }
        })
    }
    render() {
        return (
            <React.Fragment>
                {
                    this.state.step===1?
                    <Step1 onChange={(templetId)=>{this._changeTempletId(templetId)}} selectedRowKeys={this.state.templetId}/>:
                    <Step2 onChange={(fieldList)=>{this._changePlanList(fieldList)}} templetId={this.state.templetId} selectedRowKeys={this.state.fieldList}/>
                }
                <div style={{textAlign:"right"}}>
                    {this.state.step===2?<Button onClick={this._preStep} >上一步</Button>:null}
                    {this.state.step===2?<Button onClick={this._preview} style={{marginLeft:"10px"}}>预 览</Button>:null}
                    {this.state.step===2?<Button onClick={this._export} style={{marginLeft:"10px"}} type="primary">导出并完成</Button>:null}
                    {this.state.step===1?<Button onClick={this._nextStep} style={{marginLeft:"10px"}} type="primary">下一步</Button>:null}
                    <Button style={{marginLeft:"10px"}} onClick={()=>{this._cancle()}}>取 消</Button>
                </div>
                <Modal
                    visible={this.state.showPDF}
                    title={<span style={{fontWeight:"bold"}}>预览打印</span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <PreviewDoc id={this.state.templetId[0]} type='summaryPreview' planTaskIds={this.state.fieldList}/>
                </Modal>
            </React.Fragment>
        )
    }
}
