import React, { Component } from 'react';
import { Button, Modal} from 'antd';
import styles from './index.module.less';
import SearchBox from './searchBox';
import IndexTable from './indexTable';
import SummaryTable from './summaryModal';
import {hasButtonPermission} from '@/util'

export default class SummaryReocrdManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            filterValue:{
                name:"",
                createTimeStart:"",
                createTimeEnd:"",
                templetId:"",
            },
            showSummaryModal:false,//显示新建汇总记录弹窗
            summaryStep:1,//当前新建步骤
        }
    }
    _search = (params) => {
        this.setState({filterValue:{...params}});
    }
    addPlan = () => {
        this.setState({
            showSummaryModal:true
        })
    }
    _onClose = (reload) => {
        this.setState({showSummaryModal:false,summaryStep:1});
        if(reload==="reload"){
            this.setState({
                filterValue:{
                    name:"",
                    createTimeStart:"",
                    createTimeEnd:"",
                    templetId:"",
                }
            })
        }
    }
    //修改弹窗
    _changeTitle = (currentStep) => {
        this.setState({summaryStep:currentStep})
    }
    render() {
        return (
            <div className={styles.container}>
                <div className={styles.card}>
                    <div className={styles.cardTitle}>
                        汇总记录管理
                    </div>
                    <SearchBox search = {this._search}/>
                    {
                        hasButtonPermission("summaryRecord001")?<div className={styles.filterBox}>
                        <Button type="primary" onClick={this.addPlan}>汇总数据</Button>
                    </div>:null
                    }
                    <IndexTable filterValue={this.state.filterValue}/>
                </div>
                <Modal
                    visible={this.state.showSummaryModal}
                    title={`汇总数据-${this.state.summaryStep===1?"选择汇总数据":"选择数据范围"}`}
                    onCancel={this._onClose}
                    destroyOnClose={true}
                    width={1000}
                    footer={false}
                >
                    <SummaryTable onCancel={(reload)=>{this._onClose(reload)}} changeStep={(currentStep)=>{this._changeTitle(currentStep)}}/>
                </Modal>
            </div>
        )
    }
}